import React from 'react';

export const Card = props => {
  return (
    <div className="card hoverable">
      <div className="card-body">
        <h4 className="card-title">
          <a>{props.title}</a>
        </h4>
        <p className="card-text">{props.children}</p>
        <a href="#" className="btn btn-primary">
          View Campaign
        </a>
      </div>
    </div>
  );
};

export const Jumbotron = props => {
  return (
    <div className="row">
      <div className="col-md-12 z-depth-4">
        <div
          className="card card-image"
          style={{
            backgroundImage:
              'url(https://ak2.picdn.net/shutterstock/videos/11752292/thumb/1.jpg)',
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center'
          }}
        >
          <div className="text-white text-center py-5 px-4 my-5 animated fadeIn">
            <div>
              <h1 className="card-title pt-3 mb-5 font-bold display-4">
                <strong>{props.title}</strong>
              </h1>
              <p className="mx-5 mb-5">{props.content}</p>
              <a className="btn btn-outline-white btn-rounded">
                <i className="fa fa-plus left" /> {props.buttonText}
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};



